// src/script.js
// Full client + renderer. Adds:
// - linkify bare URLs
// - make "Likely Locations:" into Google Maps links
// - render standalone image URLs as clickable thumbnails
// - preserves all previous behavior (drag/drop, enter key, mutual exclusivity)

import { Client, handle_file } from "@gradio/client";

const HF_SPACE = "Mostafa-XS1/Flower_Identifier";

window.addEventListener("DOMContentLoaded", () => {
  const textEl = document.getElementById("userTextInput");
  const fileEl = document.getElementById("userImageInput");
  const dropZone = document.getElementById("dropZone");
  const fileStatus = document.getElementById("fileStatus");
  const outEl = document.getElementById("modelOutput");
  const submitBtn = document.getElementById("submitBtn");
  const clearBtn = document.getElementById("clearBtn");

  if (!textEl || !fileEl || !dropZone || !fileStatus || !outEl || !submitBtn || !clearBtn) {
    console.error("Missing required DOM elements.");
    return;
  }

  // Press Enter => trigger identify
  textEl.addEventListener("keydown", (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      submitBtn.click();
    }
  });

  function setSelectedFile(file) {
    if (file) {
      fileStatus.textContent = `${file.name} (${Math.round(file.size / 1024)} KB)`;
      textEl.value = "";
      textEl.disabled = true;
    } else {
      fileStatus.textContent = "No file chosen";
      textEl.disabled = false;
    }
  }

  fileEl.addEventListener("change", () => {
    const f = (fileEl.files && fileEl.files[0]) || null;
    setSelectedFile(f);
  });

  // clickable drop zone
  dropZone.addEventListener("click", () => {
    if (fileEl.disabled) return;
    fileEl.click();
  });

  // drag & drop
  dropZone.addEventListener("dragover", (e) => { e.preventDefault(); dropZone.classList.add("drag-over"); });
  dropZone.addEventListener("dragleave", () => { dropZone.classList.remove("drag-over"); });
  dropZone.addEventListener("drop", (e) => {
    e.preventDefault();
    dropZone.classList.remove("drag-over");
    const dt = e.dataTransfer;
    if (!dt || !dt.files || dt.files.length === 0) return;
    const file = dt.files[0];
    try {
      const dataTransfer = new DataTransfer();
      dataTransfer.items.add(file);
      fileEl.files = dataTransfer.files;
    } catch (err) {
      // fallback
      fileEl._tempFile = file;
    }
    setSelectedFile(file);
  });

  clearBtn.addEventListener("click", () => {
    textEl.value = "";
    textEl.disabled = false;
    try { fileEl.value = ""; fileEl._tempFile = undefined; } catch {}
    setSelectedFile(null);
    outEl.textContent = "Model output will appear here.";
  });

  submitBtn.addEventListener("click", async () => {
    outEl.textContent = "Waiting for model response...";
    submitBtn.disabled = true;

    const text = textEl.value.trim();
    const file = (fileEl.files && fileEl.files[0]) || fileEl._tempFile || null;

    if (!text && !file) {
      outEl.textContent = "Please provide text or an image.";
      submitBtn.disabled = false;
      return;
    }
    if (text && file) {
      outEl.textContent = "Please provide either text OR an image, not both.";
      submitBtn.disabled = false;
      return;
    }

    try {
      const client = await Client.connect(HF_SPACE);
      let payload;
      if (text) {
        payload = { typed_name: text };
      } else {
        if (!file) throw new Error("No file available to upload.");
        const wrapped = await handle_file(file);
        payload = { image: wrapped, typed_name: "" }; // typed_name required by API
      }

      const res = await client.predict("/classify", payload);
      const primary = (res && res.data) ? (Array.isArray(res.data) ? res.data[0] : res.data) : (typeof res === "string" ? res : JSON.stringify(res));
      renderMarkdownResponse(primary, outEl);
    } catch (err) {
      console.error("Submit error:", err);
      outEl.textContent = "Error: " + (err?.message || String(err));
    } finally {
      submitBtn.disabled = false;
    }
  });
});


// ---------- Markdown-ish renderer with linkification & location handling ----------

function escapeHtml(s) {
  return String(s)
    .replaceAll("&", "&amp;")
    .replaceAll("<", "&lt;")
    .replaceAll(">", "&gt;")
    .replaceAll('"', "&quot;")
    .replaceAll("'", "&#39;");
}

function parseFencedCodeBlocks(text) {
  const blocks = [];
  const re = /```([\w+-]*)\n([\s\S]*?)```/g;
  let i = 0;
  const out = text.replace(re, (m, lang, code) => {
    blocks.push({ lang: lang || "", code: escapeHtml(code) });
    return `@@CODEBLOCK${i++}@@`;
  });
  return { text: out, blocks };
}
function restoreFenced(text, blocks) {
  return text.replace(/@@CODEBLOCK(\d+)@@/g, (m, idx) => {
    const b = blocks[Number(idx)];
    const cls = b.lang ? ` class="lang-${escapeHtml(b.lang)}"` : "";
    return `<pre><code${cls}>${b.code}</code></pre>`;
  });
}

function renderInline(md) {
  // images from markdown
  md = md.replace(/!\[([^\]]*)\]\((https?:\/\/[^\s)]+)\)/g, (m, alt, url) =>
    `<img src="${url}" alt="${escapeHtml(alt)}" style="max-width:100%;height:auto">`
  );

  // markdown links [text](https://...)
  md = md.replace(/\[([^\]]+)\]\((https?:\/\/[^\s)]+)\)/g, (m, label, url) =>
    `<a href="${url}" target="_blank" rel="noopener noreferrer">${label}</a>`
  );

  // inline code
  md = md.replace(/`([^`]+)`/g, (m, code) => `<code>${escapeHtml(code)}</code>`);

  // bold
  md = md.replace(/\*\*([^*]+)\*\*/g, "<strong>$1</strong>");

  // italic (avoid bold)
  md = md.replace(/(^|[^*])\*([^*][^*]*?)\*([^*]|$)/g, (m, pre, txt, post) => `${pre}<em>${txt}</em>${post}`);

  // ---- plain (bare) URL -> anchor (images handled above)
  // Replace plain URLs that are not already inside tags. Since we just created <a> and <img>, this runs after those.
  md = md.replace(/(^|[^"'>])((https?:\/\/[^\s<>()]+))/g, (m, prefix, url) => {
    // keep prefix (char before url) and replace url with anchor
    // if url looks like an image, render as clickable thumbnail below (we'll replace standalone img URLs in post-processing)
    return `${prefix}<a href="${url}" target="_blank" rel="noopener noreferrer">${url}</a>`;
  });

  return md;
}

function renderBlocks(md) {
  const lines = md.split("\n");
  let html = "";
  let inUL = false, inOL = false;
  for (let raw of lines) {
    const line = raw.replace(/\s+$/,""); // rtrim
    if (!line.trim()) {
      if (inUL) { html += "</ul>"; inUL = false; }
      if (inOL) { html += "</ol>"; inOL = false; }
      html += "<br>";
      continue;
    }

    if (/^###\s+/.test(line)) { if(inUL){html += "</ul>";inUL=false;} if(inOL){html += "</ol>";inOL=false;} html += `<h3>${line.replace(/^###\s+/,"")}</h3>`; continue; }
    if (/^##\s+/.test(line))  { if(inUL){html += "</ul>";inUL=false;} if(inOL){html += "</ol>";inOL=false;} html += `<h2>${line.replace(/^##\s+/,"")}</h2>`; continue; }
    if (/^#\s+/.test(line))   { if(inUL){html += "</ul>";inUL=false;} if(inOL){html += "</ol>";inOL=false;} html += `<h1>${line.replace(/^#\s+/,"")}</h1>`; continue; }

    if (/^\>\s+/.test(line)) { if(inUL){html += "</ul>";inUL=false;} if(inOL){html += "</ol>";inOL=false;} html += `<blockquote>${line.replace(/^\>\s+/, "")}</blockquote>`; continue; }

    const uMatch = line.match(/^(?:\s*)[-*+]\s+(.*)/);
    if (uMatch) {
      if (inOL) { html += "</ol>"; inOL = false; }
      if (!inUL) { html += "<ul>"; inUL = true; }
      html += `<li>${uMatch[1]}</li>`;
      continue;
    }

    const oMatch = line.match(/^(?:\s*)(\d+)\.\s+(.*)/);
    if (oMatch) {
      if (inUL) { html += "</ul>"; inUL = false; }
      if (!inOL) { html += "<ol>"; inOL = true; }
      html += `<li>${oMatch[2]}</li>`;
      continue;
    }

    if (inUL) { html += "</ul>"; inUL = false; }
    if (inOL) { html += "</ol>"; inOL = false; }
    html += `<p>${line}</p>`;
  }
  if (inUL) html += "</ul>";
  if (inOL) html += "</ol>";
  return html;
}

function postProcessHtml(html) {
  let out = String(html);

  // 1) Turn <p>https://...(image)</p> into clickable thumbnail
  out = out.replace(/<p>\s*(https?:\/\/[^\s<]+?\.(?:png|jpe?g|gif|webp))\s*<\/p>/gi, (m, url) => {
    const safeUrl = url;
    return `<p><a href="${safeUrl}" target="_blank" rel="noopener noreferrer"><img src="${safeUrl}" alt="" style="max-width:220px;border-radius:8px;display:block;margin-top:6px;"></a></p>`;
  });

  // 2) Convert any remaining standalone <p>URL</p> into an anchor (if not already)
  out = out.replace(/<p>\s*(https?:\/\/[^\s<]+)\s*<\/p>/gi, (m, url) => {
    return `<p><a href="${url}" target="_blank" rel="noopener noreferrer">${url}</a></p>`;
  });

  // 3) Convert "Likely Locations: a, b, c" paragraphs into linked locations
  out = out.replace(/<p>\s*Likely Locations:\s*([^<]+)<\/p>/gi, (m, list) => {
    const parts = list.split(/\s*,\s*/).map(s => s.trim()).filter(Boolean);
    const links = parts.map(p => `<a href="https://www.google.com/maps/search/${encodeURIComponent(p)}" target="_blank" rel="noopener noreferrer">${escapeHtml(p)}</a>`);
    return `<p><strong>Likely Locations:</strong> ${links.join(', ')}</p>`;
  });

  // 4) Ensure links inside result-locations get the hover color (CSS already provides)
  return out;
}

function mdToHtml(raw) {
  let s = String(raw || "").replace(/^Model output\s*/i, "").trim();

  const { text, blocks } = parseFencedCodeBlocks(s);
  const escaped = escapeHtml(text);
  const inlined = renderInline(escaped);
  const body = renderBlocks(inlined);
  const restored = restoreFenced(body, blocks);
  const post = postProcessHtml(restored);
  return post.replace(/(<br>\s*){3,}/g, "<br><br>");
}

function renderMarkdownResponse(primary, containerEl) {
  if (!containerEl) return;
  try {
    const html = mdToHtml(primary);
    containerEl.innerHTML = html || "<i>(no content)</i>";
  } catch (err) {
    console.error("render error:", err);
    containerEl.textContent = typeof primary === "string" ? primary : JSON.stringify(primary, null, 2);
  }
}
